import React, { useState, useEffect, useRef } from 'react';
import { useRouter } from 'next/navigation'; // If using Next.js 13+

function Page() {
  const [img, setImg] = useState(null);
  const router = useRouter();
  const finBuddyRef = useRef(null);

  useEffect(() => {
    const storedImg = localStorage.getItem("photo");
    setImg(storedImg);

    const user = localStorage.getItem("user");
    if (!user) {
      router.push("/");
    }
  }, [router]); // Ensure proper dependency management

  return (
    <div className='h-screen w-full p-10'>
      <nav className='fixed top-0 h-20 w-full flex items-center justify-between px-10 gap-10'>
        <div>
          <h1 className='text-3xl'>FinBubby</h1>
        </div>
        <div className='flex gap-10 items-center justify-center'>
          <h1>Home</h1>
          <h1>Spendings</h1>
          <h1>Forecast</h1>
          <h1>Suggestion</h1>

          <div className='h-10 w-10 rounded-3xl overflow-hidden'>
            <img src={img || ""} alt="User" />
          </div>
        </div>
      </nav>

      <div>
        <h1 className='text-4xl text-blue-600'>Spendings</h1>
      </div>
    </div>
  );
}

export default Page;
